.card-nav-container {
  position: absolute;
  top: 2em;
  left: 50%;
  transform: translateX(-50%);
  width: 90%;
  max-width: 800px;
  z-index: 99;
  box-sizing: border-box;
}

.card-nav {
  display: block;
  height: 60px;
  padding: 0;
  background: linear-gradient(135deg, #0a0a0a 0%, #1a1a1a 100%);
  border: 1px solid rgba(0, 255, 136, 0.3);
  border-radius: 0.75rem;
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.5), 0 0 20px rgba(0, 255, 136, 0.2);
  position: relative;
  overflow: hidden;
  will-change: height;
  backdrop-filter: blur(10px);
}

.card-nav-top {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  height: 60px;
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 0.5rem 0.45rem 0.55rem 1.1rem;
  z-index: 2;
}

.hamburger-menu {
  height: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  cursor: pointer;
  gap: 6px;
}

.hamburger-menu:hover .hamburger-line {
  opacity: 0.75;
}

.hamburger-line {
  width: 30px;
  height: 2px;
  background-color: currentColor;
  transition:
    transform 0.25s ease,
    opacity 0.2s ease,
    margin 0.3s ease;
  transform-origin: 50% 50%;
}

.hamburger-menu.open .hamburger-line:first-child {
  transform: translateY(4px) rotate(45deg);
}

.hamburger-menu.open .hamburger-line:last-child {
  transform: translateY(-4px) rotate(-45deg);
}

.logo-container {
  display: flex;
  align-items: center;
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
}

.logo {
  height: 28px;
}

.card-nav-cta-button {
  background: linear-gradient(135deg, #00ff88 0%, #00cc6a 100%);
  color: #000;
  border: none;
  border-radius: calc(0.75rem - 0.35rem);
  padding: 0 1rem;
  height: 100%;
  font-weight: 600;
  cursor: pointer;
  transition: all 0.3s ease;
  box-shadow: 0 0 15px rgba(0, 255, 136, 0.4), 0 2px 4px rgba(0, 0, 0, 0.3);
  text-shadow: 0 0 10px rgba(0, 0, 0, 0.3);
}

.card-nav-cta-button:hover {
  background: linear-gradient(135deg, #00cc6a 0%, #00ff88 100%);
  box-shadow: 0 0 25px rgba(0, 255, 136, 0.6), 0 4px 8px rgba(0, 0, 0, 0.4);
  transform: translateY(-2px);
}

.card-nav-content {
  position: absolute;
  left: 0;
  right: 0;
  top: 60px;
  bottom: 0;
  padding: 0.5rem;
  display: flex;
  align-items: flex-end;
  gap: 12px;
  visibility: hidden;
  pointer-events: none;
  z-index: 1;
}

.card-nav.open .card-nav-content {
  visibility: visible;
  pointer-events: auto;
}

.nav-card {
  height: 100%;
  flex: 1 1 0;
  min-width: 0;
  border-radius: calc(0.75rem - 0.2rem);
  position: relative;
  display: flex;
  flex-direction: column;
  padding: 12px 16px;
  gap: 8px;
  user-select: none;
  border: 1px solid rgba(0, 255, 136, 0.1);
  box-shadow: 0 4px 15px rgba(0, 0, 0, 0.3), 0 0 20px rgba(0, 255, 136, 0.05);
  backdrop-filter: blur(10px);
  transition: all 0.3s ease;
}

.nav-card:hover {
  border-color: rgba(0, 255, 136, 0.2);
  box-shadow: 0 6px 20px rgba(0, 0, 0, 0.4), 0 0 30px rgba(0, 255, 136, 0.1);
  transform: translateY(-2px);
}

.nav-card-label {
  font-weight: 600;
  font-size: 22px;
  letter-spacing: -0.5px;
  text-shadow: 0 0 10px rgba(255, 255, 255, 0.1);
}

.nav-card-links {
  margin-top: auto;
  display: flex;
  flex-direction: column;
  gap: 2px;
}

.nav-card-link {
  font-size: 16px;
  cursor: pointer;
  text-decoration: none;
  transition: all 0.3s ease;
  display: inline-flex;
  align-items: center;
  gap: 6px;
  color: #cccccc;
  text-shadow: 0 0 5px rgba(255, 255, 255, 0.1);
}

.nav-card-link:hover {
  color: #00ff88;
  text-shadow: 0 0 10px rgba(0, 255, 136, 0.3);
  transform: translateX(2px);
}

@media (max-width: 768px) {
  .card-nav-container {
    width: 90%;
    top: 1.2em;
  }

  .card-nav-top {
    padding: 0.5rem 1rem;
    justify-content: space-between;
  }

  .hamburger-menu {
    order: 2;
  }

  .logo-container {
    position: static;
    transform: none;
    order: 1;
  }

  .card-nav-cta-button {
    display: block;
  }

@media (max-width: 768px) {
  .card-nav-cta-button {
    display: none;
  }
}

  .language-switcher {
    display: none;
  }

@media (max-width: 768px) {
  .language-switcher {
    display: flex;
    align-items: center;
    justify-content: center;
    gap: 0;
    background: rgba(0, 0, 0, 0.8);
    border: 1px solid rgba(0, 255, 136, 0.3);
    border-radius: 30px;
    padding: 4px;
    margin: 1rem 0.5rem;
    margin-top: 2rem;
    width: 120px;
    height: 40px;
    margin-left: auto;
    margin-right: auto;
    box-shadow: 0 0 20px rgba(0, 255, 136, 0.2);
  }

  .language-option {
    padding: 8px 16px;
    border-radius: 26px;
    border: none;
    background: transparent;
    color: rgba(255, 255, 255, 0.7);
    cursor: pointer;
    transition: all 0.3s ease;
    font-size: 14px;
    font-weight: 500;
    flex: 1;
    height: 32px;
    display: flex;
    align-items: center;
    justify-content: center;
    min-width: 0;
  }

  .language-option[data-active="true"] {
    background: linear-gradient(135deg, #00ff88 0%, #00cc6a 100%);
    color: #000;
    font-weight: 600;
    box-shadow: 0 0 15px rgba(0, 255, 136, 0.4);
  }

  .language-option:hover:not([data-active="true"]) {
    color: rgba(0, 255, 136, 0.8);
  }
}

  .mobile-lang-button:hover {
    background: linear-gradient(135deg, #00cc6a 0%, #00ff88 100%);
    transform: translateY(-2px);
  }

  .card-nav-content {
    flex-direction: column;
    align-items: stretch;
    gap: 8px;
    padding: 0.5rem;
    bottom: 0;
    justify-content: flex-start;
  }

  .nav-card {
    height: auto;
    min-height: 60px;
    flex: 1 1 auto;
    max-height: none;
  }

  .nav-card-label {
    font-size: 18px;
  }

  .nav-card-link {
    font-size: 15px;
  }
}
